.trigger {
  padding: 0 24px;
  font-size: 18px;
  line-height: 64px;
  cursor: pointer;
  transition: color 0.3s;
}

.trigger:hover {
  color: #1890ff;
}

.logo {
  height: 32px;
  margin: 16px 4px;
  text-align: center;
  color: #fff;
  font-size: 20px;
  font-family: cursive;
  font-weight: lighter;
  cursor: pointer;
}

.site-layout .site-layout-background {
  background: #fff;
}

.ant-layout-has-sider {
  height: 100%;
}

span.ant-tag {
  margin: 4px;
}

.ant-layout-sider-children {
  height: 100vh !important;
}

.error-screen {
  border: 1px solid red;
  padding: 12px;
  background: lightyellow;
}
.count-number {
  font-size: 32px;
  font-weight: lighter;
}
.count-card {
  width: 16vw;
  background-color: #1890ff !important;
}
.card-content > h2,
.card-content > h1 {
  color: #fff !important;
}
.card-content > h2 {
  text-align: left;
}

.hidden {
  visibility: hidden;
  opacity: 0;
}
.visible {
  visibility: visible;
  opacity: 1;
  transition: opacity 1s linear, visibility 2s linear;
}

#device-stats .count-card {
  width: 24vw;
}

#app-stats .count-card {
  width: 24vw;
}

/* Toast CSS */

#snackbar {
  visibility: hidden;
  min-width: 250px;
  margin-left: -125px;
  background-color: #333;
  color: #fff;
  text-align: center;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 1;
  left: 50%;
  bottom: 30px;
  font-size: 17px;
}

#snackbar.show {
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

@-webkit-keyframes fadein {
  from {bottom: 0; opacity: 0;} 
  to {bottom: 30px; opacity: 1;}
}

@keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 30px; opacity: 1;}
}

@-webkit-keyframes fadeout {
  from {bottom: 30px; opacity: 1;} 
  to {bottom: 0; opacity: 0;}
}

@keyframes fadeout {
  from {bottom: 30px; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}